html, body {
  margin:0;
  padding: 0;
  width:100%;
  height:100%;
}

.chat-wrapper {
  position: absolute;
  top:40px;
  right: 40px;
  box-sizing: border-box;
  width: 400px;
  height: 647px;
  border: 1px solid #ccc;
  border-radius: 5px;
  display: block;
}

.chat-frame {
  width: 500px;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
}